html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fontInter);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --fontInter: "Inter", sans-serif;
}

:root {
  --green: hsl(163, 72%, 41%);
  --red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagramStart: hsl(37, 97%, 70%);
  --instagramEnd: hsl(5, 77%, 71%);
  --YouTube: hsl(348, 97%, 39%);
  --toggleBackgroundStart: hsl(210, 79%, 56%);
  --toggleBackgroundEnd: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --darkBackground: hsl(230, 17%, 14%);
  --darkTopBackground: hsl(232, 19%, 15%);
  --darkCard: hsl(228, 28%, 20%);
  --darkText1: hsl(230, 22%, 74%);
  --darkText2: hsl(0, 100%, 100%);
  --lightBackground: hsl(0, 100%, 100%);
  --lightTopBackground: hsl(225, 100%, 98%);
  --lightCard: hsl(227, 47%, 96%);
  --lightText1: hsl(228, 12%, 44%);
  --lightText2: hsl(230, 17%, 14%);
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}